$white: #fff;
$white-light-gray: #f9f9f9;
$header-black: #242a32;
$input-border-color: #D5D5D7;
$button-color: #0058E6;
$button-hover-color: #0048bd;
$button-loading-color: #d1345b;
$button-loading-color-alt: #f1295b;
@mixin user-select() {
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
}

*:focus {
    outline: none;
}

#adminmenu li#toplevel_page_jkit-dashboard .wp-menu-image img {
    width: 16px;
}

.jkit-dashboard-header-wrap {
    margin-right: 20px;
    a {
        text-decoration: none;
        color: #444;
        &:hover {
            color: #222;
        }
    }
    .jkit-dashboard-header-tab {
        padding: 0;
        margin: 1em 0;
        border-bottom: 1px solid #ddd;
        a {
            font-size: 15px;
            margin-bottom: -2px;
            padding: 20px;
            display: inline-block;
            border-bottom: 3px solid transparent;
            &.tab-active {
                border-bottom: 3px solid #0835e6;
            }
        }
    }
}

.jkit-button {
  background-color: $button-color;
  box-shadow: none;
  border: none;
  border-radius: 5px;
  color: $white;
  padding: 12px 25px;
  cursor: pointer;
  font-size: 14px;
  text-transform: capitalize;

  &:hover {
    background-color: $button-hover-color;
  }
}

.jkit-loading {
    text-align: center;
    line-height: 100px;
}

#jkit-dashboard-header-container {
    text-align: center;
    height: calc(100vh - 200px);
    min-height: 150px;
    position: relative;
    margin-right: 20px;
    h1 {
        font-size: 32px;
        line-height: 35px;
    }
    p {
        font-size: 15px;
    }
    .empty-content {
        width: 100%;
        position: absolute;
        top: 50%;
        -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
        .empty-content-wrapper {
            width: 450px;
            margin: 0 auto;
        }
        p {
            margin: 25px 0;
        }
        h1 {
            margin-top: 0;
        }
    }
}

.jkit-dashboard-body-wrap {
    margin-right: 20px;
    #jkit-form-content {
        margin: 20px 0 0;
        * {
            box-sizing: border-box;
        }
    }
    .jkit-form-content {
        position: relative;
        display: block;
        height: 50px;
        margin: 0;
        padding: 0 87px 0 10px;
        font-size: 0;
        white-space: nowrap;
        cursor: pointer;
        @include user-select();
        &:before,
        &:after {
            content: '';
            display: inline-block;
            vertical-align: middle;
        }
        &:before {
            height: 100%;
        }
        &:after {
            position: absolute;
            width: 24px;
            height: 100%;
            right: 30px;
            background-image: url('data:image/svg+xml;base64,');
            background-repeat: no-repeat;
            background-position: center;
            transform: rotate(180deg);
        }
    }
    .jkit-form-content>div {
        display: inline-block;
        vertical-align: middle;
    }
    @media screen and (max-width: 800px) {
        .jkit-form-content {
            height: auto;
            padding: 10px 87px 10px 30px;
            &:before {
                display: none;
            }
            &:after {
                top: 0;
            }
        }
        .jkit-form-content>div {
            display: block;
            width: 100%;
        }
    }
    .jkit-form-tab {
        position: relative;
        width: 100%;
        margin: 0 auto 10px;
        border-radius: 4px;
        background-color: $white;
        box-shadow: 0 0 0 1px rgba(236, 236, 236, 1);
        opacity: 1;
        transition: box-shadow .2s, opacity .4s;
        &:hover {
            box-shadow: 0 4px 10px 0 rgba(0, 0, 0, .11);
        }
    }
    .jkit-form-content+.jkit-form-tab-content {
        transition: max-height .3s;
    }
    .jkit-form-tab:not(.collapse) {
        .jkit-form-content+.jkit-form-tab-content {
            max-height: 0;
            overflow: hidden;
            padding: 0;
        }
    }
    .jkit-form-tab-content {
        background-color: $white-light-gray;
        text-align: justify;
        padding: 25px;
    }
    .jkit-form-input-group {
        >label {
            font-weight: 700;
            margin-bottom: 10px;
            display: block;
            color: $header-black;
            font-size: 14px;
            line-height: 16px;
        }
        >input {
            border-radius: 5px;
            padding: 8px 30px;
            height: 54px;
            box-sizing: border-box;
            font-size: 14px;
            line-height: 28px;
            display: inline-block;
            color: $header-black;
            border: 1px solid $input-border-color;
            box-shadow: none;
            width: 100%;
        }
        &:not(:last-child) {
            margin-bottom: 15px;
        }
    }
    .jkit-form-info {
        width: 60%;
        white-space: nowrap;
        font-size: 0;
        &:before {
            content: '';
            display: inline-block;
            height: 44px;
            vertical-align: middle;
        }
    }
    .jkit-form-name {
        display: inline-block;
        width: 75%;
        margin-left: 15px;
        font-weight: 700;
        font-size: 14px;
        text-transform: uppercase;
        line-height: 22px;
        color: $header-black;
        white-space: normal;
        text-align: left;
        vertical-align: middle;
    }
    .jkit-form-submit {
        margin-top: 30px;
        text-align: right;
        >button {
            padding: 8px 20px 8px 20px;
            color: $white;
            cursor: pointer;
            @include user-select();
            position: relative;
            z-index: 1;
            font-size: 16px;
            border-style: solid;
            border-width: 0;
            background-color: $button-color;
            box-shadow: none;
            border-radius: 5px;
            margin-top: 0;
            margin-bottom: 0;
            line-height: 26px;
            &.saving,
            &.saving:hover {
                background-color: $button-loading-color;
            }
            &:hover {
                background-color: $button-hover-color;
            }
        }
    }
    .elements-control-container {
        display: flex;
        flex-flow: row wrap;
        align-items: center;
        .element-checkbox-option {
            position: relative;
            display: flex;
            justify-content: space-between;
            width: calc((100% / 4) - (30px * 3 / 4));
            align-items: center;
            font-size: 24px;
            margin-bottom: 30px;
            padding: 15px;
            box-sizing: border-box;
            border-radius: 5px;
            background-color: rgb(255, 255, 255);
            box-shadow: 0 0 0 1px rgba(236, 236, 236, 1);
            @media screen and (min-width: 1025px) {
                &:not(:nth-child(4n)) {
                    margin-right: 30px;
                }
            }
            @media screen and (min-width: 769px) and (max-width: 1024px) {
                width: calc((100% / 3) - (30px * 2 / 3));
                &:not(:nth-child(3n)) {
                    margin-right: 30px;
                }
            }
            @media screen and (min-width: 481px) and (max-width: 768px) {
                width: calc((100% / 2) - (30px / 2));
                &:not(:nth-child(2n)) {
                    margin-right: 30px;
                }
            }
            @media screen and (max-width: 480px) {
                width: 100%;
            }
            &:hover {
                box-shadow: 0 4px 10px 0 rgba(0, 0, 0, .11);
            }
            .element-title {
                margin-left: 15px;
                font-weight: 700;
                font-size: 14px;
                text-transform: uppercase;
                line-height: 22px;
                color: #242a32;
                white-space: normal;
                text-align: left;
                vertical-align: middle;
            }
            .element-toggle {
                position: absolute;
                top: 50%;
                right: 0;
                transform: translateY(-50%);
                margin: 0 15px 0 0;
            }
            input[type="checkbox"] {
                display: none;
            }
            input:checked + .switch {
                background: #d4e4ff;
                &:after {
                    background: #5f9bff;
                    transform: translate3d(100%, -50%, 0);
                }
            }
            .switch {
                border: 1px solid rgba(0, 0, 0, 0.1);
                margin-top: 5px;
                margin-right: 15px;
                display: inline-block;
                width: 35px;
                height: 12px;
                border-radius: 8px;
                background: #ebebeb;
                vertical-align: middle;
                position: relative;
                cursor: pointer;
                user-select: none;
                transition: background 350ms ease;
                &:after {
                    content: "";
                    background: #ccc;
                    border: 1px solid rgba(0, 0, 0, 0.1);
                    transform: translate3d(0, -50%, 0);
                    display: block;
                    width: 20px;
                    height: 20px;
                    border-radius: 50%;
                    position: absolute;
                    top: 50%;
                    left: -3px;
                    transition: all 350ms cubic-bezier(0, 0.95, 0.38, 0.98), background 150ms ease;
                }
            }
        }
    }
    .elements-global-control {
        margin-bottom: 2em;
        text-align: center;
        .enable-all,
        .disable-all {
            user-select: none;
            -webkit-user-select: none;
            -khtml-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            -o-user-select: none;
            position: relative;
            z-index: 1;
            font-size: 16px;
            border-style: solid;
            border-width: 0;
            box-shadow: none;
            border-radius: 5px;
            margin-top: 0;
            margin-bottom: 0;
            line-height: 26px;
        }
        .enable-all {
            background-color: $button-color;
            margin-right: 5px;
            &:hover {
                background-color: $button-hover-color;
            }
        }
        .disable-all {
            background-color: $button-loading-color;
            &:hover {
                background-color: $button-loading-color-alt;
            }
        }
    }
}

.jkit-condition-add {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 40px;

  button {
    background-color: $button-color;
    box-shadow: none;
    border: none;
    border-radius: 5px;
    color: $white;
    margin-left: 10px;
    padding: 10px 20px;
    cursor: pointer;
    text-transform: capitalize;

    &:hover {
      background-color: $button-hover-color;
    }
  }
}

.jkit-condition-container {
  padding: 0 20px;

  .jkit-condition-empty {
    text-align: center;
    max-width: 350px;
    margin: 50px auto 30px;

    p {
      font-size: 15px;
    }
  }

  .jkit-condition-item {
    margin: 10px 0;

    .jkit-condition-header {
      padding: 12px;
      background: #f9f9f9;
      color: #000;
      cursor: pointer;
      position: relative;
      font-weight: bold;
      border: 1px solid #e0e0e0;

      .tab-delete {
        position: absolute;
        right: 10px;
        font-size: 16px;
        top: 12px;
      }
    }

    .jkit-condition-content {
      display: none;
      padding: 20px;
      border: 1px solid #e0e0e0;
      border-top: 0;
    }
  }
}

.jkit-element-container {
  margin-bottom: 15px;
  position: relative;

  &.ui-sortable-placeholder {
    background: #cad5ff !important;
    visibility: visible !important;
    width: 100% !important;
    border: 1px dashed #a2b5ff;
  }

  .jkit-container-header {
    border: 1px solid #e0e0e0;
    background: #fbfbfb;
    padding: 10px 15px;
    font-weight: 500;
    cursor: pointer;
    box-sizing: border-box;
    position: relative;

    h3 {
      display: inline-block;
      font-size: 13px;
      margin: 0;

      i {
        margin-right: 10px;
        color: #333;
        font-size: 12px;
      }
    }

    .jkit-header-action {
      position: absolute;
      right: -1px;
      top: -1px;
      height: 100%;
      vertical-align: middle;
      border: 1px solid #ddd;
      background: #fff;

      > div {
        float: right;
        display: inline-block;
        width: 60px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        border-right: 1px solid #dddddd;

        a {
          display: block;
        }
      }
    }
  }

  .jkit-container-body {
    display: none;
    padding: 5px 10px;
    border: 1px solid #e0e0e0;
    box-sizing: border-box;
    border-top: none;
    background: #fff;
  }
}

.jkit-condition-global {
  display: none;
}

.content-exist {
  width: 500px;
  margin: 50px auto;
  text-align: left;

  .jkit-condition-empty {
    margin: 30px auto;

    h1 {
      font-size: 25px !important;
    }
  }

  .active-element-wrapper {
    background: #fff;
    padding: 40px 50px;
    margin: 40px -80px 40px;
    box-shadow: 0px 0px 5px rgba(16, 16, 16, 0.1);
    border-radius: 3px;

    h2 {
      text-align: left;
      margin-top: 0;
      margin-bottom: 40px;
    }

    .active-element-heading {
      padding: 0 20px;
      font-size: 13px;
      font-weight: bold;

      > ul {
        > li {
          display: inline-block;
        }
      }

      .name {
        width: 265px;
      }

      .priority, .edit, .clone, .delete {
        width: 60px;
      }
    }

    .content-button {
      text-align: center;
      margin-top: 20px;
    }
  }

  .inactive-element-wrapper {
    .tab-priority, .tab-clone {
      display: none !important;
    }
  }
}

.content-body {
  min-height: 100px;
  border: 1px dashed #ddd;
  padding: 20px;
}



#jkit-builder-container {
  text-align: center;
  position: relative;
  margin-right: 20px;
  min-height: 300px;

  h1 {
    font-size: 32px;
    line-height: 35px;
  }

  p {
    font-size: 15px;
  }

  .empty-content {
    width: 100%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

    .empty-content-wrapper {
      width: 450px;
      margin: 0 auto;
    }

    p {
      margin: 25px 0;
    }

    h1 {
      margin-top: 0;
    }
  }
}

.create-element-builder-overlay {
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 99999;
  display: none;
}

.create-element-builder-wrapper {
  background: #fff;
  z-index: 99999;
  position: fixed;
  margin-right: auto;
  margin-left: auto;
  max-height: 90vh;
  right: auto;
  left: 50px;
  overflow: hidden;
  top: 10vh;
  height: auto;
  display: none;
}

.popup-option {
  .popup-header {
    padding: 25px 20px;
    color: #000;
    position: relative;
    border-bottom: 1px solid #e4e4e4;

    h2 {
      padding: 0;
      color: #000;
      margin: 0;
    }

    .close {
      position: absolute;
      right: 20px;
      top: 24px;
      background: #fff;
      color: #3a3a3a;
      line-height: 20px;
      height: 20px;
      width: 20px;
      text-align: center;
      cursor: pointer;
      font-size: 16px;
    }
  }

  .popup-content {
    overflow: hidden;
    overflow-y: auto;
  }

  .popup-footer {
    padding: 20px;
    text-align: right;
    border-top: 1px solid #e4e4e4;

    .close, .generate {
      cursor: pointer;
      padding: 10px 30px;
      display: inline-block;
      font-size: 14px;
    }

    .close {
      background: #fff;
      border: 1px solid #ddd;
      border-radius: 5px;
    }

    .generate {
      background-color: $button-color;
      box-shadow: none;
      border: none;
      border-radius: 5px;
      color: $white;
      margin-left: 10px;

      &:hover {
        background-color: $button-hover-color;
      }
    }
  }

  .popup-body {
    .tabbed-body {
      width: auto;
      padding: 0;
      height: auto;
      overflow-y: scroll;
      float: none;
    }
  }
}

.jeg_metabox_normal_segment {
  .jeg_metabox_body {
    padding: 10px 20px 0;
  }

  .widget-wrapper {
    margin-bottom: 0;
  }

  .widget-left {
    display: none !important;
  }
}
